
import Drawer from 'vux/src/components/drawer/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


import { mapGetters } from 'vuex'
export default {
components: {
Drawer,
ViewBox,
XHeader,
Group,
Cell,
XButton,Flexbox, FlexboxItem
},
data() {
return {

drawerVisibility: false,
defaultProps: {
children: 'childOrgs',
label: 'chineseName'
},
isShow:false,
selectData:{
data:{},
selfIsSelected:false,
childrenHasSelected:false
}
};
},
created() {
this.$store.dispatch('OrgStructTree/getOrgStruct')
},
computed:{
...mapGetters('OrgStructTree',{
data:'getOrgStruct'
}),
isShowNav () {
if (window.location.href.indexOf('hide-nav') > -1) {
return false
}
return true
},
},
methods: {
handleNodeClick(data,node,indeterminate) {

console.log(data);
},
handleCheckChange(data, checked, indeterminate) {//复选框选中事件

this.selectData.data = data
this.selectData.selfIsSelected = checked
this.selectData.childrenHasSelected = indeterminate
}
}
};



